import axios from 'axios'
export default {
  name: 'list',
  data () {
    return {
      deleteProject: false,
      list: [],
      pname: '',
      pid: Number
    }
  },
  methods: {
    getProjectList () {
      // axios.get('/static/testData/projectList.json')
      axios.get(this.url + '/project/all')
        .then(this.getProjectList)
    },
    getProjectListSucc (res) {
      if (res.status === 200) {
        const data = res.data
        this.list = data
      }
    },
    handleDelete (pid, pname) {
      this.deleteProject = true
      this.pname = pname
      this.pid = pid
    },
    delProject () {
      this.$axios({
        method: 'delete',
        url: this.url + '/project/id/' + this.pid
      }).then((res) => {
        if (res.status === 200) {
          alert('删除成功！')
          axios.get(this.url + '/project/all')
            .then(this.getProjectListSucc)
          this.deleteProject = false
        }
      }).catch((err) => {
        alert(err)
      })
    }
  },
  mounted () {
    this.getProjectList()
  }
}



